'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { mockAnomaService, MockMatch, MockIntent } from '@/lib/mockAnomaService';

interface IntentMatchesProps {
  intentId?: string;
  onMatchSelect?: (match: MockMatch) => void;
}

export default function IntentMatches({ intentId, onMatchSelect }: IntentMatchesProps) {
  const [matches, setMatches] = useState<MockMatch[]>([]);
  const [userIntents, setUserIntents] = useState<MockIntent[]>([]);
  const [selectedIntentId, setSelectedIntentId] = useState<string>(intentId || '');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const currentUser = mockAnomaService.getCurrentUser();

  useEffect(() => {
    if (currentUser) {
      loadUserIntents();
    }
  }, [currentUser]);

  useEffect(() => {
    if (selectedIntentId) {
      findMatches(selectedIntentId);
    }
  }, [selectedIntentId]);

  const loadUserIntents = async () => {
    if (!currentUser) return;

    try {
      const intents = await mockAnomaService.getIntents({ userId: currentUser.id });
      setUserIntents(intents.filter(intent => intent.status === 'active'));
      
      // Auto-select first intent if none selected
      if (!selectedIntentId && intents.length > 0) {
        setSelectedIntentId(intents[0].id);
      }
    } catch (err) {
      setError('Failed to load your intents');
    }
  };

  const findMatches = async (intentId: string) => {
    setIsLoading(true);
    setError(null);

    try {
      const foundMatches = await mockAnomaService.findMatches(intentId);
      setMatches(foundMatches);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to find matches');
      setMatches([]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleCreateMatch = async (match: MockMatch) => {
    try {
      const createdMatch = await mockAnomaService.createMatch(
        match.teacherIntentId,
        match.learnerIntentId
      );
      onMatchSelect?.(createdMatch);
      
      // Refresh matches
      if (selectedIntentId) {
        findMatches(selectedIntentId);
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to create match');
    }
  };

  if (!currentUser) {
    return (
      <Card className="mystical-card">
        <CardContent className="pt-6 text-center">
          <div className="text-4xl mb-4">üîí</div>
          <h3 className="text-xl font-bold text-theme-white mb-2">Wallet Not Connected</h3>
          <p className="text-gray-300 mb-4">
            Please connect your Anoma wallet to view matches.
          </p>
        </CardContent>
      </Card>
    );
  }

  if (userIntents.length === 0) {
    return (
      <Card className="mystical-card">
        <CardContent className="pt-6 text-center">
          <div className="text-4xl mb-4">üìù</div>
          <h3 className="text-xl font-bold text-theme-white mb-2">No Active Intents</h3>
          <p className="text-gray-300 mb-4">
            You need to post an intent before you can find matches.
          </p>
          <Button className="btn-primary">
            Post Your First Intent
          </Button>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="space-y-6">
      {/* Intent Selector */}
      <Card className="mystical-card">
        <CardHeader>
          <CardTitle className="text-theme-white">Your Active Intents</CardTitle>
          <CardDescription className="text-gray-300">
            Select an intent to find compatible matches
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid gap-3">
            {userIntents.map((intent) => (
              <div
                key={intent.id}
                className={`p-4 rounded-lg border cursor-pointer transition-all ${
                  selectedIntentId === intent.id
                    ? 'border-theme-red bg-theme-red/10 glow-red'
                    : 'border-theme-white/20 hover:border-theme-white/40'
                }`}
                onClick={() => setSelectedIntentId(intent.id)}
              >
                <div className="flex items-center justify-between">
                  <div>
                    <div className="flex items-center gap-2 mb-1">
                      <Badge className={
                        intent.intentType === 'teach' 
                          ? 'bg-theme-green/20 text-theme-green' 
                          : 'bg-theme-red/20 text-theme-red'
                      }>
                        {intent.intentType === 'teach' ? 'Teaching' : 'Learning'}
                      </Badge>
                      <span className="text-theme-white font-medium">{intent.skillName}</span>
                    </div>
                    <div className="text-sm text-gray-300">
                      Level: {intent.skillLevel} ‚Ä¢ {intent.location || 'Location not specified'}
                    </div>
                  </div>
                  {selectedIntentId === intent.id && (
                    <div className="w-3 h-3 bg-theme-red rounded-full animate-pulse"></div>
                  )}
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Error Display */}
      {error && (
        <Alert className="border-theme-red bg-theme-red/10">
          <AlertDescription className="text-theme-red">
            {error}
          </AlertDescription>
        </Alert>
      )}

      {/* Matches Display */}
      {selectedIntentId && (
        <Card className="mystical-card">
          <CardHeader>
            <CardTitle className="text-theme-white flex items-center gap-2">
              üéØ Compatible Matches
              {isLoading && (
                <div className="w-4 h-4 border-2 border-theme-white border-t-transparent rounded-full animate-spin"></div>
              )}
            </CardTitle>
            <CardDescription className="text-gray-300">
              Found {matches.length} compatible {matches.length === 1 ? 'match' : 'matches'} for your intent
            </CardDescription>
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <div className="space-y-4">
                {[1, 2, 3].map((i) => (
                  <div key={i} className="animate-pulse">
                    <div className="h-32 bg-theme-black/50 rounded-lg"></div>
                  </div>
                ))}
              </div>
            ) : matches.length === 0 ? (
              <div className="text-center py-8">
                <div className="text-4xl mb-4">üîç</div>
                <h3 className="text-lg font-semibold text-theme-white mb-2">No Matches Found</h3>
                <p className="text-gray-300 mb-4">
                  No compatible matches found for this intent yet. Try adjusting your preferences or check back later.
                </p>
                <Button 
                  onClick={() => selectedIntentId && findMatches(selectedIntentId)}
                  className="btn-secondary"
                >
                  Refresh Matches
                </Button>
              </div>
            ) : (
              <div className="space-y-4">
                {matches.map((match) => {
                  const otherUser = match.teacherUser.id === currentUser?.id ? match.learnerUser : match.teacherUser;
                  const isTeaching = match.teacherUser.id === currentUser?.id;
                  
                  return (
                    <div key={match.id} className="mystical-card border border-theme-white/20 hover-glow-green">
                      <div className="p-6">
                        <div className="flex items-start justify-between mb-4">
                          <div className="flex-1">
                            <div className="flex items-center gap-2 mb-2">
                              <div className="w-10 h-10 bg-gradient-red-green rounded-full flex items-center justify-center text-white font-bold">
                                {otherUser.username?.charAt(0).toUpperCase() || 'U'}
                              </div>
                              <div>
                                <h3 className="font-semibold text-theme-white">
                                  {otherUser.username || 'Anonymous User'}
                                </h3>
                                <p className="text-sm text-gray-300">
                                  {isTeaching ? 'Wants to learn' : 'Can teach'} {match.skill}
                                </p>
                              </div>
                            </div>
                            
                            {otherUser.bio && (
                              <p className="text-sm text-gray-300 mb-3">{otherUser.bio}</p>
                            )}
                            
                            <div className="flex items-center gap-4 text-sm text-gray-300 mb-3">
                              {otherUser.location && (
                                <span>üìç {otherUser.location}</span>
                              )}
                            </div>
                          </div>
                          
                          <div className="text-right">
                            <div className="text-2xl font-bold text-theme-green mb-1">
                              {match.compatibilityScore}%
                            </div>
                            <div className="text-xs text-gray-400">Compatibility</div>
                          </div>
                        </div>
                        
                        {/* Match Reasons */}
                        <div className="mb-4">
                          <div className="text-sm text-gray-400 mb-2">Why this is a good match:</div>
                          <div className="flex flex-wrap gap-2">
                            {match.matchReasons.map((reason, index) => (
                              <Badge key={index} className="bg-theme-white/10 text-theme-white text-xs">
                                {reason}
                              </Badge>
                            ))}
                          </div>
                        </div>
                        
                        {/* Actions */}
                        <div className="flex gap-3">
                          <Button
                            onClick={() => handleCreateMatch(match)}
                            className="btn-primary flex-1"
                          >
                            Start Conversation
                          </Button>
                          <Button className="btn-outline">
                            View Profile
                          </Button>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            )}
          </CardContent>
        </Card>
      )}
    </div>
  );
}
